<template>
    <div id="accordionPhoneFilter" class="panel-group">
        <div id="filtersPanel" class="panel panel-default">
            <div class="panel-heading"
                v-for="(filterKind, kindKey, index) in filters"
                :key="index">
                <h3 class="panel-title text-center">
                    <a data-toggle="collapse"
                        data-parent="#accordionPhoneFilter"
                        :href="'#accordionPhoneFilter' + kindKey"
                        :class="{
                            'color-cta-diseable': !filtersData[kindKey].isActive,
                            'color-cta-enable': filtersData[kindKey].isActive
                        }">
                        {{ $t(kindKey) }}
                        <span :class="{
                            'glyphicon': true,
                            'glyphicon-menu-down': true,
                            'color-cta-diseable': !filtersData[kindKey].isActive,
                            'color-cta-enable': filtersData[kindKey].isActive
                        }"></span>
                        <span :class="{
                            'glyphicon': true,
                            'glyphicon-menu-up': true,
                            'color-cta-diseable': !filtersData[kindKey].isActive,
                            'color-cta-enable': filtersData[kindKey].isActive
                        }"></span>
                    </a>
                </h3>
            </div>
            <div class="panel-heading">
                <h3 class="panel-title text-center">
                    <a data-toggle="collapse"
                        data-parent="#accordionPhoneFilter"
                        href="#accordionPhoneFilterSort"
                        :class="{
                            'color-cta-diseable':!sorts.modelName.isActive &&
                                                    !sorts.popularity.isActive &&
                                                    !sorts.price.isActive,
                            'color-cta-enable': sorts.modelName.isActive ||
                                                sorts.popularity.isActive ||
                                                sorts.price.isActive
                        }">
                        {{ $t('sortBy') }}
                        <span :class="{
                            'glyphicon': true,
                            'glyphicon-menu-down': true,
                            'color-cta-diseable':!sorts.modelName.isActive &&
                                                    !sorts.popularity.isActive &&
                                                    !sorts.price.isActive,
                            'color-cta-enable': sorts.modelName.isActive ||
                                                sorts.popularity.isActive ||
                                                sorts.price.isActive
                        }"></span>
                        <span :class="{
                            'glyphicon': true,
                            'glyphicon-menu-up': true,
                            'color-cta-diseable': !sorts.modelName.isActive &&
                                                    !sorts.popularity.isActive &&
                                                    !sorts.price.isActive,
                            'color-cta-enable': sorts.modelName.isActive ||
                                                sorts.popularity.isActive ||
                                                sorts.price.isActive
                        }"></span>
                    </a>
                </h3>
            </div>
            <div class="panel-heading">
                <h3 class="panel-title text-center"
                    v-on:click="onResetAllFilters()">
                    <a href="#"
                        :class="{
                            'color-cta-enable': (this.isFiltersActive() || this.isSortsActive()),
                            'color-cta-diseable':
                                !(this.isFiltersActive() || this.isSortsActive()),
                            }"
                        >{{ $t('clearAll') }}</a>
                </h3>
            </div>
        </div>

        <div class="panel panel-default">
            <div class="panel-collapse collapse"
                v-for="(filterKind, kindKey, index) in filters"
                :id="'accordionPhoneFilter' + kindKey"
                :key="index">
                <div class="panel-body">
                    <ul>
                        <li v-for="(filter, filterKey, index) in filterKind"
                            :key="index">
                            <button type="button"
                                :class="{
                                    'btn': true,
                                    'btn-success': filter.isActive,
                                    'btn-danger': !filter.isActive,
                                }"
                                v-on:click="setFilterOption({ kindKey, filterKey, filter, })">
                                    {{ translateFilterOption(kindKey, filter.text) }}
                                    <span
                                        class="badge"
                                        :class="{
                                            'badge': true,
                                            'opacity-hidden': !filter.isActive,
                                        }">{{ filter.qty }}</span>
                            </button>
                        </li>
                    </ul>
                </div>
            </div>
            <div id="accordionPhoneFilterSort" class="panel-collapse collapse">
                <div class="panel-body">
                    <ul>
                        <li v-for="(sort, kindKey, index) in sorts"
                            :key="index">
                            <button type="button"
                                :class="{
                                    'btn': true,
                                    'btn-success': sort.isActive,
                                    'btn-danger': !sort.isActive,
                                }"
                                v-on:click="setSortOption({ kindKey, sort })">
                                    {{ translateSortOption(kindKey, sort.text) }}
                                    <span :class="{
                                        'glyphicon': true,
                                        'glyphicon-sort-by-attributes':
                                                sort.reverse && sort.isActive,
                                        'glyphicon-sort-by-attributes-alt':
                                            !sort.reverse && sort.isActive,
                                    }"></span>
                            </button>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<i18n>
    {
        "en": {
            "brand": "Brand",
            "os": "Os",
            "price": "Price",
            "memory": "Memory",
            "sortBy": "Sort By",
            "clearAll": "Clear All"
        },
        "fr": {
            "brand": "Marque",
            "os": "Système",
            "price": "Prix",
            "memory": "Mémoire",
            "sortBy": "Trier Par",
            "clearAll": "Réinitialiser"
        }
    }
</i18n>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
    name: 'ModalChoosePhonePhonesFilters',
    computed: {
        ...mapGetters({
            sorts: 'phones/sorts',
            filters: 'phones/filters',
            filtersData: 'phones/filtersData',
        }),
    },
    methods: {
        ...mapActions('phones/', [
            'resetAllFilters',
            'setFilterOption',
            'setSortOption',
        ]),
        translateFilterOption(kindKey, text) {
            if (this.$i18n.locale === 'fr') {
                if (kindKey === 'memory') { return text.replace('GB', 'Go').replace('GB', 'Go'); }
            }
            return text;
        },
        translateSortOption(kindKey, text) {
            if (this.$i18n.locale === 'fr') {
                if (text === 'Popularity') { return 'Popularité'; }
                if (text === 'Model Name') { return 'Nom du model'; }
                if (text === 'Price') { return 'Prix'; }
            }
            return text;
        },
        isFiltersActive() {
            return this.filtersData.brand.isActive ||
                this.filtersData.os.isActive ||
                this.filtersData.price.isActive ||
                this.filtersData.memory.isActive;
        },
        isSortsActive() {
            return this.sorts.popularity.isActive ||
                this.sorts.modelName.isActive ||
                this.sorts.price.isActive;
        },
        onResetAllFilters() {
            if (this.isFiltersActive() || this.isSortsActive()) {
                this.resetAllFilters();
            }
        },
    },
};
</script>

<style lang="scss" scoped>
    @import './ModalChoosePhonePhonesFilters.scss';
</style>



