<template>
    <div class="col-xs-6 col-md-4">
        <button
            data-dismiss="modal"
            :class="{'box-device': true, 'active': isActive }"
            @click="$emit('set-chosen-phone', { phone })">
            <div class="illustrate">
                <img style="height:100px;width:50px;"
                    :src="`${staticUrl}/img/phone/${phone.slug}.png`"
                    :alt="phone.name">
                <span class="capacity">{{ translateMemory(phone.modelMemoryName) }}</span>
            </div>
            <div class="price-block">
                <span class="price-from">{{ $t('from') }}</span>
                <span class="price">
                    $<span itemprop="price">
                        {{ Math.floor(phone.modelPrice) }}
                        <sup>
                            <i style="display:none">.</i>
                            {{ (phone.modelPrice + '').slice(-2).replace('0', '') }}
                        </sup>
                    </span>
                </span>
            </div>
            <span class="modele">{{ phone.name }}</span>
            <span class="brand">{{ phone.brandName }}</span>
        </button>
    </div>
</template>

<i18n>
    {
        "en": {
            "from": "from"
        },
        "fr": {
            "from": "à partir de"
        }
    }
</i18n>

<script>
export default {
    name: 'ModalChoosePhoneBodyPhone',
    props: {
        phone: Object,
        staticUrl: String,
        isActive: Boolean,
    },
    methods: {
        translateMemory(text) {
            if (this.$i18n.locale === 'fr') {
                return text.replace('GB', 'Go').replace('MB', 'Mo');
            }
            return text;
        },
    },
};
</script>



