import * as types from '../../mutation-types';

/* eslint-disable */

//  CONSIDER PUTTING CODE BELLOW IN ANOTHER FILE AND IMPORT IT
const pRange = 100;
const mRange = 8;

function initFilters(state, phone, isActive, isDisabled) {
    if (!state.filters.brand[phone.brandSlug]) {
        state.filters.brand[phone.brandSlug] = {
            text: phone.brandName, isActive, isDisabled, qty: 1,
        };
    } else {
        state.filters.brand[phone.brandSlug].qty += 1;
    }

    if (!state.filters.os[phone.osSlug]) {
        state.filters.os[phone.osSlug] = { text: phone.osName, isActive, isDisabled, qty: 1 };
    } else {
        state.filters.os[phone.osSlug].qty += 1;
    }

    const priceRangeText = Math.floor(phone.modelPrice / pRange) * pRange;

    if (!state.filters.price[priceRangeText]) {
        state.filters.price[priceRangeText] = {
            text: `${(priceRangeText) ? priceRangeText - 1 : 0} - ${priceRangeText + pRange} $`,
            isActive,
            isDisabled,
            qty: 1,
        };
    } else {
        state.filters.price[priceRangeText].qty += 1;
    }

    const memoryRangeText = Math.floor(phone.modelMemorySize / (mRange * 1000)) * mRange;

    if (!state.filters.memory[memoryRangeText]) {
        state.filters.memory[memoryRangeText] = {
            text: `${memoryRangeText || '< 8'} GB`,
            isActive,
            isDisabled,
            qty: 1,
        };
    } else {
        state.filters.memory[memoryRangeText].qty += 1;
    }
}

function toggleAllFilter(state, kindKey, filter) {
    let filterQty = 0;
    let filterActiveQty = 0;

    for (var f in state.filters[kindKey]) {
        if (state.filters[kindKey][f].isActive) { filterActiveQty += 1; }
        filterQty += 1;
    }

    if (filterActiveQty == filterQty || (filterActiveQty == 1 && filter.isActive)) {
        for (var f in state.filters[kindKey]) {
            state.filters[kindKey][f].isActive = filterActiveQty == 1;
        }
    }
    return (filterActiveQty == 1 && filter.isActive);
}

function updateFiltersCount(state) {
    const filteredPhones = getters.filteredPhones(state);

    let memoryRange, priceRange;

    state.phones.forEach(phone => {
        memoryRange = Math.floor(phone.modelMemorySize / (mRange * 1000)) * mRange;
        priceRange = Math.floor(phone.modelPrice / pRange) * pRange;
        state.filters.brand[phone.brandSlug].qty = 0;
        state.filters.os[phone.osSlug].qty = 0;
        state.filters.price[priceRange].qty = 0;
        state.filters.memory[memoryRange].qty = 0;
    });

    filteredPhones.forEach(filteredPhone => {
        memoryRange = Math.floor(filteredPhone.modelMemorySize / (mRange * 1000)) * mRange;
        priceRange = Math.floor(filteredPhone.modelPrice / pRange) * pRange;
        state.filters.brand[filteredPhone.brandSlug].qty++;
        state.filters.os[filteredPhone.osSlug].qty++;
        state.filters.price[priceRange].qty++;
        state.filters.memory[memoryRange].qty++;
    });

    for (var filterKind in state.filters) {
        state.filtersData[filterKind].isActive = false;

        for (var filter in state.filters[filterKind]) {
            if (state.filters[filterKind][filter].qty == 0) {
                state.filters[filterKind][filter].isDisabled = true;
            } else {
                state.filters[filterKind][filter].isDisabled = false;
            }

            if (!state.filters[filterKind][filter].isActive) {
                state.filtersData[filterKind].isActive = true;
            }
        }
    }
}

function sortFilters(state) {
    let filtersOs = {}, filtersBrand = {};
    Object.keys(state.filters.os)
        .sort()
        .forEach(function(v, i) {
            filtersOs[v] = state.filters.os[v];
        });
    Object.keys(state.filters.brand)
        .sort()
        .forEach(function(v, i) {
            filtersBrand[v] = state.filters.brand[v];
        });
    state.filters.os = filtersOs;
    state.filters.brand = filtersBrand;
}

// initial state
const state = {
    chosenPhoneModelSlug: '',
    phones: [],
    fixedPhones: [],
    sorts: { popularity: {}, modelName: {}, price: {} },
    filters: { brand: {}, os: {}, price: {}, memory: {} },
    filtersData: { brand: {}, os: {}, price: {}, memory: {} },
};

// getters
const getters = {
    phones: state => state.phones.slice(),
    chosenPhone: state => state.phones.find(p => p.modelSlug === state.chosenPhoneModelSlug) || {},
    sorts: state => Object.assign({}, state.sorts),
    filters: state => Object.assign({}, state.filters),
    filtersData: state => Object.assign({}, state.filtersData),
    filteredPhones: state => state.phones.filter(phone => {
        if (!state.filters.brand[phone.brandSlug].isActive) { return false; }
        if (!state.filters.os[phone.osSlug].isActive) { return false; }

        let priceRange = Math.floor(phone.modelPrice / pRange) * pRange;
        let memoryRange = Math.floor(phone.modelMemorySize / (mRange * 1000)) * mRange;

        if (!state.filters.price[priceRange].isActive) { return false; }
        if (!state.filters.memory[memoryRange].isActive) { return false; }

        return true;
    }).slice(),
    chosenPhoneModelSlug: state => state.chosenPhoneModelSlug,
};

// actions
const actions = {
    setSortOption({ commit }, { kindKey, sort }) {
        commit(types.EDIT_SORT_OPTION, { kindKey, sort });
    },
    setFilterOption({ commit }, { kindKey, filterKey, filter }) {
        commit(types.EDIT_FILTER_OPTION, { kindKey, filterKey, filter });
    },
    resetAllFilters({ commit }) {
        commit(types.RECEIVE_ALL_PHONES, { phones: state.fixedPhones.slice() });
    },
};

// mutations
const mutations = {
    [types.RECEIVE_ALL_PHONES](state, { phones }) {
        if (phones && phones.length) {
            if (state.fixedPhones && !state.fixedPhones.length) {
                state.fixedPhones = phones.slice();
            }

            state.sorts = {
                popularity: {
                    phoneKey: 'position',
                    text: 'Popularity',
                    isActive: false,
                    reverse: true,
                },
                modelName: {
                    phoneKey: 'modelSlug',
                    text: 'Model Name',
                    isActive: false,
                    reverse: true,
                },
                price: {
                    phoneKey: 'modelPrice',
                    text: 'Price',
                    isActive: false,
                    reverse: true,
                },
            };

            state.filters = { brand: {}, os: {}, price: {}, memory: {} };
            state.filtersData = { brand: {}, os: {}, price: {}, memory: {} };

            phones.forEach(phone => initFilters(state, phone, true, false));
            state.phones = phones;

            sortFilters(state);

            state.filters = Object.assign({}, state.filters);  //  TODO QUICK FIX, HAS TO BE CHANGED
            state.filtersData = Object.assign({}, state.filtersData);  //  TODO QUICK FIX, HAS TO BE CHANGED
            state.sorts = Object.assign({}, state.sorts);  //  TODO QUICK FIX, HAS TO BE CHANGED
            //  This function initFilters(state, phone, isActive) break the reactive pattern...
        }
    },

    [types.SET_CHOSEN_PHONE](state, { phone, phoneModelSlug }) {
        let condition = (phone) ? phone.modelSlug: phoneModelSlug;
        let phoneTmp = state.phones.find(p => p.modelSlug === condition);
        (phoneTmp) ? state.chosenPhoneModelSlug = phoneTmp.modelSlug : state.chosenPhoneModelSlug = 'byod';
    },

    [types.EDIT_FILTER_OPTION](state, { kindKey, filterKey, filter }) {
        let toggleAllToActive = toggleAllFilter(state, kindKey, filter);
        if (toggleAllToActive) {
            state.filters[kindKey][filterKey].isActive = filter.isActive;
        } else {
            state.filters[kindKey][filterKey].isActive = !filter.isActive;
        }
        state.filters = Object.assign({}, state.filters);  //  TODO QUICK FIX, HAS TO BE CHANGED

        updateFiltersCount(state);
        state.filters = Object.assign({}, state.filters);  //  TODO QUICK FIX, HAS TO BE CHANGED

        sortFilters(state);
        state.filters = Object.assign({}, state.filters);  //  TODO QUICK FIX, HAS TO BE CHANGED
    },

    [types.EDIT_SORT_OPTION](state, { kindKey, sort }) {
        let condition1 = sort.phoneKey, condition2 = 'position', condition3 = 'modelPrice', condition4 = 'modelSlug';

        state.phones.sort(function(a, b) {
            if (a[condition1] < b[condition1]) { return -1; }
            if (a[condition1] > b[condition1]) { return 1; }
            if (a[condition2] < b[condition2]) { return -1; }
            if (a[condition2] > b[condition2]) { return 1; }
            if (a[condition3] < b[condition3]) { return -1; }
            if (a[condition3] > b[condition3]) { return 1; }
            if (a[condition4] < b[condition4]) { return -1; }
            if (a[condition4] > b[condition4]) { return 1; }
            return 0;  //  Should not be possible
        });

        state.sorts.price.isActive = false;
        state.sorts.modelName.isActive = false;
        state.sorts.popularity.isActive = false;
        state.sorts[kindKey].isActive = true;
        state.sorts[kindKey].reverse = !state.sorts[kindKey].reverse;

        if (state.sorts[kindKey].reverse) {
            state.phones.reverse();
        }
    },
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations,
};



