<template>
    <div id="planhub-vue-app">
        <router-view/>
    </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
    name: 'App',
    created() {
        let locale = document.querySelector('html').lang;

        if (locale) {
            locale = locale.toLowerCase();
            if (locale !== 'fr') { locale = 'en'; }
        } else {
            locale = 'fr';
        }

        this.$i18n.locale = locale;

        this.getInitialState();
        setTimeout(() => this.getPhones(), 2000);
    },
    methods: {
        ...mapActions([
            'getInitialState',
            'getPhones',
        ]),
    },
};
</script>

<style lang="scss">
    @import './App.scss';
</style>



