<template>
    <div id="modal-select-device" class="modal fade" role="dialog">
        <div class="modal-dialog">
            <div class="modal-content">
                <modal-choose-phone-header></modal-choose-phone-header>
                <modal-choose-phone-body></modal-choose-phone-body>
            </div>
        </div>
    </div>
</template>

<script>
import ModalChoosePhoneHeader from './header/ModalChoosePhoneHeader';
import ModalChoosePhoneBody from './body/ModalChoosePhoneBody';

export default {
    name: 'ModalChoosePhone',
    components: {
        'modal-choose-phone-header': ModalChoosePhoneHeader,
        'modal-choose-phone-body': ModalChoosePhoneBody,
    },
};
</script>



