import api from '../api/index';
import * as types from './mutation-types';

const actions = {
    getInitialState({ commit, rootState }) {
        api.getInitialState(({ app, searchData }) => {
            const phoneModelSlug = searchData.phone || 'byod';

            commit(`bridgeState/${types.SET_INITIAL_STATE}`, { app, searchData });
            commit(`bridgeState/${types.SET_CHOSEN_PHONE}`, { rootState, phoneModelSlug });
            commit(`phones/${types.SET_CHOSEN_PHONE}`, { phoneModelSlug });
        });
    },
    resetVueInstance({ commit, rootState }) {
        commit(`phones/${types.RECEIVE_ALL_PHONES}`, { phones: rootState.phones.fixedPhones });
        commit(`bridgeState/${types.RESET_VUE_INSTANCE}`, { rootState });

        if (document.querySelector('.modal-backdrop')) {
            //  Remove Modal Overlay
            document.querySelector('.modal-backdrop').remove();
        }
    },
    getPhones({ commit, rootState }) {
        const bridgeState = rootState.bridgeState;
        const caching = bridgeState.api.caching || {};
        const app = bridgeState.app;

        if (app && caching.getPhones) {
            if (app.province === caching.getPhones.options.province.slug) {
                commit(`phones/${types.RECEIVE_ALL_PHONES}`, { phones: caching.getPhones.data });

                if (bridgeState.currentSearchData.phone) {
                    commit(`phones/${types.SET_CHOSEN_PHONE}`, { phoneModelSlug: bridgeState.currentSearchData.phone });
                }
            }
        } else {
            api.getPhones(rootState, (response) => {
                if (response.type === 'phones') {
                    commit(`bridgeState/${types.ADD_REQUEST_CACHING}`, { route: 'getPhones', response });
                    commit(`phones/${types.RECEIVE_ALL_PHONES}`, { phones: response.data });

                    if (bridgeState.currentSearchData) {
                        commit(`phones/${types.SET_CHOSEN_PHONE}`, { phoneModelSlug: bridgeState.currentSearchData.phone });
                    }
                }
            });
        }
    },
    setChosenPhone({ commit, rootState }, { phone, phoneModelSlug }) {
        commit(`phones/${types.SET_CHOSEN_PHONE}`, { phone, phoneModelSlug });
        commit(`bridgeState/${types.SET_CHOSEN_PHONE}`, { rootState, phone, phoneModelSlug });
    },
};

export default actions;



