<template>
    <div class="modal-body">
        <div class="spinner-loader"
            v-if="phones.length == 0"></div>
        <div class="row" v-if="phones.length != 0">
            <div class="col-xs-6">
                <h3>{{ filteredPhones.length }} {{ $t('totalPhones') }}</h3>
            </div>
            <div class="col-xs-6">
                <h4>
                    {{ (chosenPhone.name) ? $t('currently') : '' }}&nbsp;
                    {{ chosenPhone.name || '' }}&nbsp;
                    {{ chosenPhone.modelMemoryName || '' }}
                </h4>
            </div>
        </div>
        <div id="phone-choices" class="row"
            v-if="phones.length != 0">
            <modal-choose-phone-body-phone
                v-for="phone in filteredPhones"
                :key="phone.modelSlug"
                :staticUrl="staticUrl"
                :phone="phone"
                :isActive="isActive(phone)"
                @set-chosen-phone="setChosenPhone"></modal-choose-phone-body-phone>
        </div>
    </div>
</template>

<i18n>
    {
        "en": {
            "currently": "Currently:",
            "totalPhones": "phones found"
        },
        "fr": {
            "currently": "Choisi:",
            "totalPhones": "téléphones trouvés"
        }
    }
</i18n>

<script>
import { mapGetters, mapActions } from 'vuex';
import ModalChoosePhoneBodyPhone from './ModalChoosePhoneBodyPhone';

export default {
    name: 'ModalChoosePhoneBody',
    computed: {
        ...mapGetters({
            currentSearchData: 'bridgeState/currentSearchData',
            staticUrl: 'bridgeState/staticUrl',
            filteredPhones: 'phones/filteredPhones',
            chosenPhone: 'phones/chosenPhone',
            phones: 'phones/phones',
        }),
    },
    methods: {
        ...mapActions([
            'setChosenPhone',
        ]),
        isActive(phone) {
            return phone.modelSlug === this.chosenPhone.modelSlug;
        },
    },
    components: {
        'modal-choose-phone-body-phone': ModalChoosePhoneBodyPhone,
    },
};
</script>

<style lang="scss" scoped>
    @import './ModalChoosePhoneBody.scss';
</style>



