<template>
    <div class="modal-header">
        <div class="row">
            <div class="col-xs-12">
                <button class="close" type="button" data-dismiss="modal">&times;</button>
                <h3 class="modal-title">{{ $t('title') }}</h3>
            </div>
        </div>
        <modal-choose-phone-phones-filters></modal-choose-phone-phones-filters>
    </div>
</template>

<i18n>
    {
        "en": {
            "title": "Choose a device"
        },
        "fr": {
            "title": "Choisir un téléphone"
        }
    }
</i18n>

<script>
import ModalChoosePhonePhonesFilters from './ModalChoosePhonePhonesFilters';

export default {
    name: 'ModalChoosePhoneHeader',
    components: {
        'modal-choose-phone-phones-filters': ModalChoosePhonePhonesFilters,
    },
};
</script>

<style lang="scss" scoped>
    @media screen and (max-width: 500px) {
        .modal-header {
            padding-left: 0;
            padding-right: 0;
        }
    }
</style>



