import Vue from 'vue';
import Router from 'vue-router';
import Reset from '../containers/Reset';
import HomeSearchBar from '../containers/HomeSearchBar';

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Reset',
            component: Reset,
        },
        {
            path: '/home-search-bar',
            name: 'HomeSearchBar',
            component: HomeSearchBar,
        },
    ],
});



