import * as types from '../../mutation-types';

/* eslint-disable */

// initial state
const state = {
    app: {},
    lastSearchData: {},
    currentSearchData: {},
    api: {
        url: {
            //staticUrl: 'https://www.planhub.ca',
            staticUrl: 'http://local.www.planhub.ca',
            getPhones: '/phonesList',
        },
        caching: {},
    },
};

// getters
const getters = {
    app: state => Object.assign({}, state.app),
    staticUrl: state => state.api.url.staticUrl,
    lastSearchData: state => Object.assign({}, state.lastSearchData),
    currentSearchData: state => Object.assign({}, state.currentSearchData),
};

// mutations
const mutations = {
    [types.SET_INITIAL_STATE](state, { app, searchData }) {
        state.app = app;
        state.lastSearchData = searchData;
        state.currentSearchData = searchData;

        if (window.location.origin === 'http://localhost:8080') {
            loadDevExternalFiles();
        }
    },

    [types.ADD_REQUEST_CACHING](state, { route, response }) {
        state.api.caching[route] = response;
    },

    [types.SET_CHOSEN_PHONE](state, { rootState, phone, phoneModelSlug }) {
        let condition = (phone) ? phone.modelSlug: phoneModelSlug;

        if (rootState) {
            let phoneTmp = rootState.phones.phones.find(p => p.modelSlug === condition);
            if (phoneTmp) { setChosenPhone(state, phoneTmp); }
        }
    },

    [types.RESET_VUE_INSTANCE](state, { rootState }) {
        if (rootState.phones.chosenPhoneModelSlug == 'byod') {
            if (document.querySelector('.btn-choice-keep')) {
                document.querySelector('.btn-choice-keep').click();
            }
        }
    },
};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
};

function loadDevExternalFiles() {
    let button = document.createElement('button');
    button.setAttribute('type', 'button');
    button.innerHTML = 'Home search Bar';
    document.body.appendChild(button);

    let y = document.createElement('LINK');
    let x = document.createElement('LINK');
    y.setAttribute('rel', 'stylesheet');
    x.setAttribute('rel', 'stylesheet');
    y.setAttribute('type', 'text/css');
    x.setAttribute('type', 'text/css');
    y.setAttribute('href', 'http://local.www.planhub.ca/dev/compiled/css/defaultInline.dev.css');
    x.setAttribute('href', 'http://local.www.planhub.ca/dev/compiled/css/planhub.dev.css');
    document.head.appendChild(y);
    document.head.appendChild(x);

    let jQueryScript = document.createElement('SCRIPT');
    let azetlorScript = document.createElement('SCRIPT');
    jQueryScript.setAttribute('src', 'https://code.jquery.com/jquery-3.2.1.min.js');
    azetlorScript.setAttribute('src', '//local.www.planhub.ca/js/azetlor.js');
    document.body.appendChild(jQueryScript);
    document.body.appendChild(azetlorScript);

    setTimeout(() => {
        let scriptsUrl = [
            'https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/js/bootstrap.min.js',
            '//local.www.planhub.ca/dev/compiled/js/planhub.dev.js',
            '//localhost:35729/livereload.js'
        ];

        for (var scriptUrl of scriptsUrl) {
            let script = document.createElement('SCRIPT');
            script.setAttribute('src', scriptUrl);
            document.body.appendChild(script);
        }

        button.onclick = function(){
            window.location.hash = '#/home-search-bar';
            setTimeout(() => {
                if ($('#modal-select-device').length) {
                    $('#modal-select-device').modal('toggle');
                }
            }, 500);
        };
    }, 1500);
}

function setChosenPhone(state, phoneTmp) {
    let btnText = 'Change phone';
    let btn = document.querySelector('.btn-choice-new');

    if (btn && btn.parentElement) {
        btnText = btn.parentElement.getAttribute('data-translation2') || btnText;
    }

    let imgURL = `/img/phone/${phoneTmp.slug}.png`;
    if (btn) {
        btn.setAttribute('data-slug', phoneTmp.modelSlug);
        btn.setAttribute('data-memory', phoneTmp.modelMemorySize);
        btn.setAttribute('data-id', phoneTmp.modelId);
        btn.setAttribute('data-img', imgURL);
        btn.classList.add('phbActive');
        btn.innerHTML = btnText;
    }

    if (document.querySelector('.btn-choice-keep')) {
        document.querySelector('.btn-choice-keep').classList.remove('active');
        document.querySelector('.btn-choice-keep').classList.remove('phbActive');
    }

    if (document.querySelector('#dropdown-choice-label')) {
        document.querySelector('#dropdown-choice-label').innerHTML = phoneTmp.name;
    }

    let phoneLink = document.querySelector('div.select-phone a');
    let locale = (state.app.locale == 'en') ? '' : (state.app.locale == 'fr') ? 'fr' : 'en';
    let phonePageUrl = locale + '/' + state.app.province + '/mobile/' + phoneTmp.slug;

    if (phoneLink) {
        phoneLink.innerHTML = '<img src="' + imgURL + '"/>';
        phoneLink.setAttribute('href', phonePageUrl);
    } else {
        if (document.querySelector('.search-summary-options .select-phone')) {
            let summary = '<a class="phone-link" href="' + phonePageUrl + '"><img src="' + imgURL + '"/></a>' +
                document.querySelector('.search-summary-options .select-phone').innerHTML.trim();
            document.querySelector('.search-summary-options .select-phone').innerHTML = summary;
        }
        if (document.querySelector('.search-summary-options .select-phone > div')) {
            document.querySelector('.search-summary-options .select-phone > div').classList.add('btn-group-with-phone');
        }
        if (!state.app.widget) {  //  TODO QUICK FIX
            initBtnPhoneChoices();  //  TODO TO BE MOCK FOR WIDGET
        }
    }

    if (!state.app.widget) {  //  TODO QUICK FIX
        onSearchDataChange();  //  TODO TO BE MOCK FOR WIDGET
    }
}



