import axios from 'axios';
import mockData from './mock';

const testing = process.env.NODE_ENV === 'testing';
// const testing = process.env.NODE_ENV !== 'production';
const LATENCY = 100;

export default {
    getInitialState(cb) {
        const isWidget = (window.location.host.indexOf('www.planhub') === -1 || window.location.host.indexOf('local.www') !== -1) || window.location.host.indexOf('192.168.') !== -1;
        if (testing) {
            // eslint-disable-next-line
            app.widget = false;
            setTimeout(() => cb({ app: mockData.app, searchData: mockData.searchData }), LATENCY);
        } else {
            // eslint-disable-next-line
            if (isWidget) {
                //  TODO MOCK DATA FOR WIDGET ???
                const app = mockData.app;
                const appDataElem = document.getElementById('planhub-widget-app-data');
                if (appDataElem) {
                    app.widget = true;
                    app.domain = appDataElem.getAttribute('data-domain') || '';
                    app.locale = appDataElem.getAttribute('data-locale') || 'en';
                    app.province = appDataElem.getAttribute('data-province') || 'quebec';
                    app.version = appDataElem.getAttribute('data-version') || 'version?';
                }
                // eslint-disable-next-line
                setTimeout(() => cb({ app: app, searchData: getSearchData(app) }), LATENCY);  //  FOR DEV
            } else {
                // eslint-disable-next-line
                app.widget = false;
                // eslint-disable-next-line
                //setTimeout(() => cb({ app: mockData.app, searchData: mockData.searchData }), LATENCY);  //  FOR DEV
                // eslint-disable-next-line
                setTimeout(() => cb({ app: app, searchData: getSearchData() }), LATENCY);
            }
        }
    },
    getPhones(rootState, cb) {
        const isWidget = (window.location.host.indexOf('www.planhub') === -1 || window.location.host.indexOf('local.www') !== -1) || window.location.host.indexOf('192.168.') !== -1;
        if (testing) {
            setTimeout(() => cb(mockData.phones), LATENCY);
        } else {
            // eslint-disable-next-line
            if (isWidget) {
                //  TODO MOCK DATA FOR WIDGET ???
                // eslint-disable-next-line
                //setTimeout(() => cb(mockData.phones), LATENCY);  //  FOR DEV
                // eslint-disable-next-line
                axios.get(`
                    ${rootState.bridgeState.api.url.staticUrl}${rootState.bridgeState.api.url.getPhones}/${rootState.bridgeState.app.province}
                    `)
                    .then(response => cb(response.data))
                    .catch(() => cb({}));
            } else {
                // eslint-disable-next-line
                // setTimeout(() => cb(mockData.phones), LATENCY);
                //  TODO Change window.origin by planhub once on prod... TODO
                // eslint-disable-next-line
                axios.get(`
                    ${window.origin + rootState.bridgeState.api.url.getPhones}/${rootState.bridgeState.app.province}
                    `)
                    .then(response => cb(response.data))
                    .catch(() => cb({}));
            }
        }
    },
};

/* eslint-disable */
function getSearchData(app) {
    function getContextAsCsv() {
        return `${app.version};${app.build};${window.location.href};${app.locale};${app.province};currentCarrier`;
    }

    function getMobileSpecificData(data) {
        if (document.querySelector('#voice [selected]')) {
            data.voice = document.querySelector('#voice [selected]').value;
        }

        if (document.querySelector('#data [selected]')) {
            data.data = document.querySelector('#data [selected]').value;
        }

        let activePhoneChoice = document.querySelector('.btn-choice.phbActive');
        // BYOD is default value
        let phoneId = 2;
        data.phone = 'byod';
        data.phoneImg = '/img/phone/byod.png';

        if (activePhoneChoice && activePhoneChoice.classList && activePhoneChoice.classList.contains('btn-choice-new')) {
            phoneId = activePhoneChoice.getAttribute('data-id');
            data.phone = activePhoneChoice.getAttribute('data-slug');
            data.phoneImg = activePhoneChoice.getAttribute('data-img');
        }

        data.urlPart = data.voice + '.' + data.data;
        data.urlPart += '.' + phoneId + '.' + data.carriers;

        data.csv = getContextAsCsv() + ';' + data.voice + ';' + data.data + ';' + data.services + ';' + data.phone + ';' + data.carriers;
    }

    //  Internet 'input#sort-by') VS Mobile $('input#sort-by-hidden') ???

    let data = {
        csv: '',
        data: null,
        phone: null,
        voice: null,
        sortBy: '',  // sort default value
        urlPart: '',
        services: null,
        phoneImg: null,
        bandwidth: null,
        downloadspeed: null,
        carriers: 'all-carriers',
        currentCarrier: null
    };

    if ('mobile' == app.nav) {
        getMobileSpecificData(data);
    }

    console.log('getSearchData');
    return data;
}



